<template>
  <div class="zd-common-hp100">
    <div class="zd-common-clearFix zd-common-hp100 zd-common-ptb20">
        <div class="zd-common-fl zd-common-w200 zd-common-pl20 zd-common-pr20 zd-common-hp100 zd-common-border-right1">
          <vue-scroll>
            <p v-for="item in allSchoolArr"
               :class="[curActive === item.id ? 'zd-cm-active zd-common-p10 zd-common-cp' : 'zd-common-p10 zd-common-cp']"
               @click="changeSchool(item.id)">{{ item.name }}</p>
          </vue-scroll>
        </div>
        <div class="zd-common-fl zd-common-pl10 zd-common-hp100 zd-cm-fr">
          <vue-scroll>
            <div class="zd-common-auto" v-if="isSave" style="width: 600px;">
              <Form ref="setForm" :model="setForm" :rules="ruleValidate" :label-width="150" class="zd-common-clearFix">
                <FormItem label="是否需要收货确认：">
                  <i-switch v-model="setForm.isConfirmSwitch" class="zd-common-fl zd-common-mt8"/>
                </FormItem>
                <div class="zd-common-clearFix">
                  <FormItem label="是否需要总务审核：" class="zd-common-fl zd-common-mr10 zd-common-h38">
                    <i-switch v-model="setForm.generalCheckSwitch"/>
                  </FormItem>
                  <FormItem v-if="setForm.generalCheckSwitch" prop="generalCheck" :label-width="0" label=""
                            class="zd-common-fl zd-common-w300 zd-common-h38">
                    <Select
                      v-if="setForm.generalCheckSwitch"
                      placeholder="请输入总务负责人"
                      v-model="setForm.generalCheck"
                      filterable
                      remote
                      multiple
                      :remote-method="remoteMethod"
                      :loading="loading"
                      @on-open-change="openChange"
                    >
                      <Option v-for="option in generalLeaderArr" :value="option.id" :key="option.id">{{option.name}}</Option>
                    </Select>
                  </FormItem>
                </div>
                <div class="zd-common-clearFix">
                  <FormItem label="是否需要部门审核：" class="zd-common-fl zd-common-h38 zd-common-mr10">
                    <i-switch v-model="setForm.departCheckSwitch"/>
                  </FormItem>
                  <template v-if="setForm.departCheckSwitch" >
                    <FormItem :label-width="0" label="" prop="departCheck" class="zd-common-fl zd-common-w300">
                      <treeselect v-model="setForm.departCheck"
                                  valueFormat="object"
                                  :multiple="true"
                                  :options="treeOptions"
                                  :normalizer="normalizer"
                                  noOptionsText="暂无数据"
                                  placeholder="请选择启用部门"
                                  @close="onSelect"
                                  :clearable="false"
                                  class="zd-common-lh20"
                      ></treeselect>
                    </FormItem>
                  </template>
                </div>
                <template v-if="setForm.departCheckSwitch" v-for="(item,index) in setForm.allDepart">
                  <FormItem :label="item.name+'：'"
                            :prop="'allDepart.' + index + '.checker'"
                            :rules="{required: true, type:'array', message: '审核人不能为空', trigger:'change'}"
                            class="zd-common-fl">
                      <Select
                      class="zd-common-w300"
                      placeholder="请输入部门审核人"
                      v-model="setForm.allDepart[index].checker"
                      filterable
                      remote
                      multiple
                      :remote-method="remoteMethod"
                      :loading="loading"
                      @on-open-change="openChange"
                    >
                      <Option v-for="option in departLeaderArr" :value="option.id" :key="option.id">{{option.name}}</Option>
                    </Select>
                  </FormItem>
                  <Icon type="ios-close" class="zd-common-cp zd-common-fl zd-common-fs20 zd-common-mt8"
                        @click="delDepartHandle(index)"/>
                </template>
              </Form>
              <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
                <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="handleSubmit('setForm')" :loading="saveLoading">
                  <span v-if="!saveLoading">保存</span>
                  <span v-else>正在保存...</span>
                </Button>
                <Button @click="cancelSetHandle" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
              </div>
            </div>
            <div class=" zd-common-auto" v-else style="width: 520px;">
              <div class="zd-common-pr20">
                <p class="zd-common-clearFix">
                  <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">是否需要收货确认：</span>
                  <span class="zd-common-fl zd-common-border-bottom1 zd-common-w100 zd-common-dib zd-common-pb5">
                    {{ setForm.isConfirmSwitch ? '是' : '否' }}
                  </span>
                </p>
                <div class="zd-common-clearFix zd-common-mt20">
                  <p class="zd-common-fl">
                    <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">总务是否需要审核：</span>
                    <span class="zd-common-fl zd-common-border-bottom1 zd-common-w100 zd-common-dib zd-common-pb5">
                      {{ setForm.generalCheckSwitch ? '是' : '否' }}
                    </span>
                  </p>
                  <p class="zd-common-fl" v-if="setForm.generalCheckSwitch">
                    <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">总务审核人：</span>
                    <span class="zd-common-fl zd-common-border-bottom1 zd-common-w100 zd-common-dib zd-common-pb5">{{ generalCheckUser }}</span>
                  </p>
                </div>
                <div class="zd-common-clearFix zd-common-mt20">
                  <p class="zd-common-fl">
                    <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">部门是否需要审核：</span>
                    <span class="zd-common-fl zd-common-border-bottom1 zd-common-w100 zd-common-dib zd-common-pb5">
                      {{ setForm.departCheckSwitch ? '是' : '否' }}
                    </span>
                  </p>
                  <p class="zd-common-fl" v-if="setForm.departCheckSwitch">
                    <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">启用部门：</span>
                    <span class="zd-common-fl zd-common-border-bottom1 zd-common-w100 zd-common-dib zd-common-pb5">{{ departmentStr }}</span>
                  </p>
                </div>
                <p class="zd-common-mt20 zd-common-clearFix" v-if="setForm.departCheckSwitch">
                  <template v-for="item in departmentSettings">
                    <span class="zd-common-fl zd-common-w150 zd-common-dib zd-common-tr">{{item.departmentName}}：</span>
                    <span class="zd-common-fl zd-common-w350 zd-common-border-bottom1 zd-common-pb5">{{item.userNames}}</span>
                  </template>
                </p>
              </div>
              <div class="zd-common-tc zd-common-mt30">
                <Button type="primary" @click="editSetHandle" class="zd-common-w100 zd-common-h32 zd-common-lh8">编辑
                </Button>
              </div>
            </div>
          </vue-scroll>
        </div>
    </div>
  </div>
</template>
<script>
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  export default {
    components: {
      Treeselect
    },
    data() {
      return {
        saveLoading:false,
        isSave:false,
        setForm: {
          isConfirmSwitch:false,
          generalCheckSwitch:false,
          departCheckSwitch:false,
          generalCheck:[],
          departCheck:null,
          allDepart:[],
          id:''
        },
        ruleValidate: {
          generalCheck:[
            {required: true, type:'array', message: '不能为空', trigger: 'change'}
          ],
          departCheck:[
            {required: true, type:'array', message: '不能为空', trigger: 'change'}
          ],
        },
        //总务负责人
        generalLeaderArr:[],
        loading:false,
        generalCheckUser:'',
        //选择启用部门tree
        treeOptions:[],
        normalizer(node) {
          return {
            id: node.id,
            label: node.name,
            children: node.children,
          }
        },
        departmentStr:'',
        departmentSettings:[],
        departLeaderArr:[],
        //获取学校
        allSchoolArr:[],
        curActive:''
      }
    },
    created(){
      this.getAllSchool().then((res)=>{
        const aData = res.data;
        this.allSchoolArr = aData;
        if(aData.length>0){
          this.curActive = aData[0].id;
          this.getSet(aData[0].id);
          this.selectDepart(aData[0].id);
        }
      });
    },
    methods:{
      //切换学校
      changeSchool(id){
        this.curActive = id;
        this.getSet(id);
        this.selectDepart(id);
      },
      //获取设置
      getSet(id){
        this.generalLeaderArr = [];
        this.departLeaderArr = [];
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!getCheckSettingBySchoolId.action',
            data: {
              id:id
            }
          })
          .then((response) => {
            const aData = response.data;
            this.setForm.isConfirmSwitch = aData.receiptFlag === '1';
            this.setForm.generalCheckSwitch = aData.checkStatus === '1';
            this.setForm.departCheckSwitch = aData.deptFlag === '1';
            this.setForm.id = aData.id ? aData.id : '';

            //总务审核
            if(aData.checkStatus === '1'){
              this.generalCheckUser = aData.userNames;
              this.setForm.generalCheck = aData.userIds.split(',');
              aData.userIds.split(',').map((item,i)=>{
                this.generalLeaderArr.push({
                  id:item,
                  name:aData.userNames.split(',')[i]
                })
              });
            }

            //部门审核
            if(aData.deptFlag === '1'){
              //部门负责人
              this.departmentSettings = aData.departmentSettings;
              //部门名称
              const depart = aData.departmentSettings.map((item)=>{
                return item.departmentName;
              });
              this.departmentStr = depart.join(',');
              //部门id
              const departId = aData.departmentSettings.map((item)=>{
                return item.departmentId;
              });
              //负责人名称
              const departUser = aData.departmentSettings.map((item)=>{
                return item.userNames;
              });
              //负责人id
              const departUserId = aData.departmentSettings.map((item)=>{
                return item.userIds;
              });

              const tmpDepart = [];
              depart.map((item,i)=>{
                tmpDepart.push({
                  id:departId[i],
                  name:item,
                  checker:departUserId[i].split(',')
                })
                departUserId[i].split(',').map((item,x)=>{
                  this.departLeaderArr.push({
                    id:item,
                    name:departUser[i].split(',')[x]
                  })
                })
              })
              this.setForm.departCheck = tmpDepart;
              this.setForm.allDepart = tmpDepart;
            }

          })
          .catch((error) => {
            console.log(error);
          });
      },
      //总务负责人
      remoteMethod(name){
        this.selectLeader(name).then((res)=>{
          this.generalLeaderArr = res.data;
          this.departLeaderArr = res.data;
        })
      },
      //选择启用部门
      selectDepart(id){
        this.treeOptions= [];
        this.getDepartmentTree(id).then((res)=>{
          this.treeOptions = res.data;
        })
      },
      //选择的部门
      onSelect(value,instanceId){
        value.map((item)=>{
          this.$set(item,'checker',[]);
        });
        this.setForm.allDepart = value;
        this.setForm.departCheck = value;
        this.$refs['setForm'].validateField('departCheck');
      },
      delDepartHandle(index){
        this.setForm.allDepart.splice(index,1);
        this.setForm.departCheck.splice(index,1);
      },
      openChange(){

      },
      //保存设置
      saveSet(receiptFlag,checkStatus,userIds,deptFlag,allDepart,schoolId,id){
        let params = {};
        params['checkSettings.receiptFlag']	= receiptFlag;
        params['checkSettings.checkStatus']	= checkStatus;
        params['userIds']	= userIds;
        params['checkSettings.deptFlag']	= deptFlag;

        allDepart.map((item,index)=>{
          params['peopleSettingVOs['+index+'].departmentId']	= item.id;
          params['peopleSettingVOs['+index+'].userIds']	= item.checker.join(',');
        })

        params['checkSettings.school.id']	= schoolId;
        params['checkSettings.id']	= id;
        BaseAxios(
          {
            method: 'post',
            url: '/sr/storeroomData!saveCheckSetting.action',
            data: params
          })
          .then((response) => {
            this.isSave=false;
            this.getSet(schoolId);
          })
          .catch((error) => {
            console.log(error);
          });
      },
      handleSubmit(name) {
        if(this.setForm.generalCheckSwitch || this.setForm.departCheckSwitch){
          this.$refs[name].validate((valid) => {
            if (valid) {
              this.saveLoading=true;
              this.saveSet(this.setForm.isConfirmSwitch?'1':'0',this.setForm.generalCheckSwitch?'1':'0',this.setForm.generalCheck.join(','),this.setForm.departCheckSwitch?'1':'0',this.setForm.allDepart,this.curActive,this.setForm.id);
            }
          })
        }else{
          this.saveLoading=true;
          this.saveSet(this.setForm.isConfirmSwitch?'1':'0',this.setForm.generalCheckSwitch?'1':'0',this.setForm.generalCheck.join(','),this.setForm.departCheckSwitch?'1':'0',this.setForm.allDepart,this.curActive,this.setForm.id);
        }
      },
      cancelSetHandle(){
        this.isSave=false;
        this.setForm.isConfirmSwitch = false;
        this.setForm.generalCheckSwitch = false;
        this.setForm.departCheckSwitch = false;
      },
      //编辑设置
      editSetHandle(){
        this.isSave = true;
        this.selectDepart(this.curActive);
      }
    },
    mounted(){}
  }
</script>
<style lang="less" scoped>
  .zd-cm-fr{
    width: calc(~"100% - 201px");
  }
</style>



